'use client';
import { useState, useEffect } from 'react';

export default function Dashboard() {
  const [events, setEvents] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [newEventName, setNewEventName] = useState('');
  const [selectedEvent, setSelectedEvent] = useState(null); // Track selected event

  useEffect(() => {
    fetchEvents();
  }, []);

  const fetchEvents = async () => {
    const res = await fetch('http://localhost:5000/api/list-events');
    if (res.ok) {
      const data = await res.json();
      setEvents(data.events || []);
    }
  };

  const createEvent = async () => {
    if (!newEventName.trim()) return;

    const res = await fetch('http://localhost:5000/api/create-event', {
      method: 'POST',
      body: JSON.stringify({ eventName: newEventName }),
      headers: { 'Content-Type': 'application/json' },
    });

    if (res.ok) {
      fetchEvents();
      setNewEventName('');
      setIsModalOpen(false);
    }
  };

  const openEvent = (eventName) => {
    setSelectedEvent(eventName); // Just update state instead of routing
  };

  return (
    <div className="p-5">
      {selectedEvent ? (
        // Show Event Page UI
        <div>
          <button
            onClick={() => setSelectedEvent(null)}
            className="mb-4 px-4 py-2 rounded bg-gray-300 hover:bg-gray-400"
          >
            Back
          </button>
          <h1 className="text-2xl font-bold">Event: {selectedEvent}</h1>
          {/* Render event-specific content here */}
        </div>
      ) : (
        // Show Events List UI
        <div>
          <button
            onClick={() => setIsModalOpen(true)}
            className="bg-blue-500 text-white px-4 py-2 rounded"
          >
            Create Main Event
          </button>

          <div className="mt-5">
            {events.length > 0 ? (
              events.map((event) => (
                <div
                  key={event}
                  className="cursor-pointer p-2 border rounded mb-2 hover:bg-gray-100"
                  onClick={() => openEvent(event)}
                >
                  {event}
                </div>
              ))
            ) : (
              <p className="text-gray-500">No events yet. Create one!</p>
            )}
          </div>
        </div>
      )}

      {isModalOpen && (
        <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50">
          <div className="bg-white p-8 rounded-lg shadow-lg w-80">
            <h2 className="text-xl font-semibold mb-4">Create New Event</h2>

            <input
              type="text"
              value={newEventName}
              onChange={(e) => setNewEventName(e.target.value)}
              placeholder="Enter event name"
              className="w-full border p-2 rounded mb-4"
            />

            <div className="flex justify-end space-x-3">
              <button
                onClick={() => setIsModalOpen(false)}
                className="px-4 py-2 rounded bg-gray-300 hover:bg-gray-400"
              >
                Cancel
              </button>
              <button
                onClick={createEvent}
                className="px-4 py-2 rounded bg-blue-500 text-white hover:bg-blue-600"
              >
                Save
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
